.constructor-slider{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  font-style: normal;
  color: #3F3737;
  padding: 0;
  .text{
    &_paragraphs {
      display: flex;
      align-items: center;
      &_price {
        span {
          font-weight:  normal !important;
          font-size: 14px !important;
          text-decoration: line-through;
          margin-left: 5px;
          position: relative;
          top: -10px;
        }
      }
      p{
        padding-bottom: 20px;
        font-weight: bold;
        font-size: 24px;
      }
    }
    span {
      font-weight: bold;
      font-size: 24px;
      line-height: 29px;
    }
  }
  .content{
    display: flex;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    &_left{
      display: flex;
      align-items: center;
      width: 50%;
      & img {
        max-width: 100%;
      }
    }
    &_right{
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      width: 50%;
      padding-left: 20px;
      h4{
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        text-align: center;
      }
      .content{
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        width: 100%;
        padding: 0;
        amp-img{
          cursor: pointer;
        }
        &_list{
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          padding-bottom: 30px;
          .left-content{
            display: flex;
            width: 30%;
            cursor: pointer;
            span{
              width: 100%;
              text-align: left;
              font-weight: normal;
              font-size: 18px;
              line-height: 22px;
            }
          }
          .right-content{
            display: flex;
            width: 70%;
            justify-content: flex-end;
            &_first{
              display: flex;
              align-items: center;
              justify-content: center;
              span{
                font-weight: normal;
                font-size: 18px;
                line-height: 22px;
                padding: 0 9px;
              }
              button {
                cursor: pointer;
                width: 30px;
                height: 30px;
                border-radius: 3px;
                background-color: #43A948;
                outline: none;
                border: none;
                color: white;
                font-size: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
              }
            }
            &_last{
              display: flex;
              align-items: center;
              justify-content: space-between;
              span{
                font-weight: bold;
                font-size: 18px;
                line-height: 22px;
                padding: 0 11px;
              }
            }
          }
        }
      }
    }
    & p {
      margin: 10px 0;
    }
    & p.bold {
      font-weight: bold;
    }
  }
  @media screen and (max-width: 768px) {
    padding: 0 15px 25px 15px;
    .content{
      flex-direction: column;
      &_left, &_right{
        width: 100%;
      }
      &_left{
        height: 400px;
      }
    }
  }
  @media screen and (max-width: 450px) {
    .content {
      &_left{
        height: 210px;
      }
      &_right {
        h4 {
          font-weight: bold;
          font-size: 18px;
          line-height: 22px;
        }
        .content {
          &_list {
            padding-bottom: 10px;
            .left-content {
              display: flex;
              width: 50% !important;
              span {
                font-weight: bold;
                font-size: 18px;
                line-height: 22px;
              }
            }
            .right-content {
              width: 200px!important;
              flex-direction: column;
              align-items: flex-end;
              &_first {
                padding-bottom: 5px;

                span {
                  font-size: 15.5657px;
                  line-height: 19px;
                }
              }

              &_last {
                display: flex;
                align-items: center;
                justify-content: space-between;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .constructor-slider {
    .text {
      span, p {
        font-size: 20px;
      }
    }
    .content_right {
      h4 {
        font-size: 20px;
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .constructor-slider {
    .text {
      span, p {
        font-size: 16px;
      }
    }
    .content_right {
      h4 {
        font-size: 18px;
      }
    }
  }
}