.right-customs {
    width: calc(100% - 65% - 17.5%);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    box-sizing: border-box;
    &__bottom {
        width: 100%;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-sizing: border-box;
        background: #FFFFFF;
        box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 5px 8px 0px rgba(0,0,0,0.14), 0px 1px 14px 0px rgba(0,0,0,0.12);
        &__title {
            padding-top: 10px;
            margin-bottom: 10px;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            &__price {
                background-color: #fff;
                border: 3px solid #F89F46;
                box-shadow: 0px 0px 30px 2px rgb(190, 189, 189);
                position: relative;
                color: forestgreen;
                top: -10px;
                left: 27px;
                border-radius: 10px;
                padding: 2px 10px;
                font-weight: bold;
            }
            &_container {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                & ul {
                    width: 100%;
                    background-color: rgb(243, 243, 243);
                    border-radius: 5px;
                    padding: 0;
                    max-height: 210px;
                    box-sizing: border-box;
                    overflow-y: scroll;
                    list-style-type: none;
                    &::-webkit-scrollbar-track {
                        border-radius: 15px;
                      }
                    &::-webkit-scrollbar {
                        height: 30px;
                        width: 6px;
                      }
                    &::-webkit-scrollbar-thumb {
                        background: #43A948;
                        border-radius: 5px;
                        width: 100px !important;
                      }
                    & li {
                        width: 100%;
                        box-sizing: border-box;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        border-bottom: 2px solid #fff;
                        padding: 0 10px;
                        .left{
                            display: flex;
                            align-items: center;
                            width: 100%;
                            height: 100%;
                            .title-image{
                              display: flex;
                              justify-content: center;
                              align-items: center;
                              overflow: hidden;
                              border: none;
                              width: 50px;
                              height: 50px;
                              border-radius: 100%;
                              background-color: #fff;
                              box-shadow: 0 0 5px rgb(204, 204, 204);
                              img{
                                max-width: 90%;
                                max-height: 90%;
                              }
                            }
                            &_text{
                              display: flex;
                              flex-direction: column;
                              padding-left: 15px;
                              width: calc(100% - 50px);
                              box-sizing: border-box;
                              padding-top: 10px;
                              h3{
                                font-size: 15.5px;
                                font-weight: bold;
                                width: 100%;
                                margin: 0;
                                word-break: break-all;
                                text-align: left;
                              }
                              .right {
                                  width: 100%;
                                  display: flex;
                                  box-sizing: border-box;
                                  align-items: center;
                                  padding-left: 5px;
                                  height: 40px;
                                  & p {
                                      margin: 0;
                                  }
                                  & span {
                                      color: #000;
                                      font-size: 14px;
                                      margin: 0 5px;
                                  }
                                  & button {
                                    background-color: rgb(40, 163, 40);
                                    color: white;
                                    border-radius: 5px;
                                    margin: 0;
                                    padding: 0;
                                    height: 25px;
                                    width: 25px;
                                  }
                                  .price {
                                      span {
                                          font-weight: bold;
                                          font-size: 14px;
                                          margin-left: 10px;
                                      }
                                  }
                              }
                            }
                          }
                    }
                }
            }
            & p {
                font-weight: normal;
                font-size: 18px;
                line-height: 29px;
                margin-top: 25px;
                text-align: center;
                color: #C4C4C4;
            }
        }
        &__form {
            &__name {
                padding: 0 10px;
                & input {
                    width: 100%;
                    height: 40px;
                    padding: 10px;
                    border: 1px solid #C4C4C4;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    &:nth-child(1) {
                        border-bottom:  none;
                        border-bottom-left-radius: 0px;
                        border-bottom-right-radius: 0px;
                    }
                    &:nth-child(2) {
                        border-top-left-radius: 0px;
                        border-top-right-radius: 0px;
                    }
                    &:nth-child(3) {
                        margin-top: 15px;
                    }
                }
            }
            &__address {
                margin-top: 20px;
                display: flex;
                flex-wrap: wrap;
                align-items: baseline;
                justify-content: space-around;
                &--item {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
                & input {
                    width: 73px;
                    height: 46px;
                    border: 1px solid #C4C4C4;
                    box-sizing: border-box;
                    border-radius: 8px;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 17px;
                    outline: none;
                }
                & p {
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 17px;
                }
            }
            &__delivery {
                display: flex;
                justify-content: flex-start;padding: 0 10px;
                margin-top: 20px;
                text-align: right;
                & input {
                    display: none;
                }
                & label {
                    position: relative;
                    &::before {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        top: -5px;
                        width: 28px;
                        height: 28px;
                        border: 1px solid #C4C4C4;
                        box-sizing: border-box;
                    }
                    &::after {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        height: 7px;
                        width: 12px;
                        left: 9px;
                        top: 4px;
                        border-left: 2px solid;
                        border-bottom: 2px solid;
                        transform: rotate(-45deg);
                    }
                }
                & span {
                    margin-left: 40px;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 17px;
                }
            }
            &__option {
                margin-top: 30px;
                padding: 0 10px;
                position: relative;
                .select {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border: 1px solid #C4C4C4;
                    border-radius: 8px;
                    padding-right: 10px;
                    height: 40px;
                    border-bottom-left-radius: 0px;
                    border-bottom-right-radius: 0px;
                    & img {
                        width: 8px;
                        height: 5px;
                    }
                    & select {
                        z-index: 1;
                        width: 100%;
                        border: none;
                        padding: 0 10px;
                        -webkit-appearance: none;
                        background-color: white;
                        appearance: none;
                        outline: none;
                        cursor: pointer;
                    }
                }
                & input {
                    width: 100%;
                    height: 40px;
                    padding: 10px;
                    border: 1px solid #C4C4C4;
                    border-top: none;
                    box-sizing: border-box;
                    border-radius: 8px;
                    border-top-left-radius: 0px;
                    border-top-right-radius: 0px;
                    outline: none;
                    &:last-child {
                        margin-top: 30px;
                        border-radius: 8px;
                    }
                }
            }
            &__bonuses-use {
                margin-top: 10px;
                display: flex;
                flex-direction: column;
                &__switcher {
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                    & span {
                        font-size: 12px;
                        margin-left: 5px;
                    }
                    & input[type="checkbox"] {
                        position: relative;
                        width: 40px;
                        height: 20px;
                        border: 1px solid rgb(184, 184, 184);
                        -webkit-appearance: none;
                        outline: none;
                        background-color: white;
                        border-radius: 10px;
                    }
                    & input:checked[type="checkbox"] {
                        background-color: white;
                    }
                    & input[type="checkbox"]:before {
                        content : '';
                        position: absolute;
                        width: 12px;
                        height: 12px;
                        border-radius: 10px;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 10%;
                        background: rgb(184, 184, 184);
                        transition: .5s;
                    }
                    
                    & input:checked[type="checkbox"]:before {
                        left: 20px;
                        background-color: #43a948;
                    }
                 }
                 &__slidecontainer {
                     margin-top: 10px;
                     opacity: 0;
                     height: 0px;
                     transition: 1s;

                     &__text {
                         display: flex;
                     }
                    .slider {
                        -webkit-appearance: none;
                        width: 100%;
                        height: 15px;
                        padding: 0;
                        border-radius: 20px;
                        background: #d3d3d3;
                        outline: none;
                        opacity: 0.7;
                        -webkit-transition: .2s;
                        transition: opacity .2s;
                    }
                    
                    .slider:hover {
                        opacity: 1;
                    }
                    
                    .slider::-webkit-slider-thumb {
                        -webkit-appearance: none;
                        appearance: none;
                        width: 22px;
                        height: 22px;
                        border-radius: 50%;
                        background: #43a948;
                        cursor: pointer;
                    }
                    
                    .slider::-moz-range-thumb {
                        width: 22px;
                        height: 22px;
                        border-radius: 50%;
                        background: #43a948;
                        cursor: pointer;
                    }
                 }
            }
            &__checkbox {
                text-align: left;
                padding: 0 10px;
                margin-top: 20px;
                display: flex;
                & input {
                    display: none;
                }
                & label {
                    position: relative;
                    &::before {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        top: -5px;
                        width: 28px;
                        height: 28px;
                        border: 1px solid #C4C4C4;
                        box-sizing: border-box;
                    }
                    &::after {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        height: 7px;
                        width: 12px;
                        left: 9px;
                        top: 4px;
                        border-left: 2px solid;
                        border-bottom: 2px solid;
                        transform: rotate(-45deg);
                    }
                }
                & span {
                    margin-left: 40px;
                    font-weight: normal;
                    font-size: 14px;line-height: 17px;
                }
            }
            &__area {
                text-align: left;
                padding: 0 20px 0 10px;
                margin-top: 20px;
                & textarea {
                    outline: none;
                    width: 100%;
                    height: 100px;
                    padding: 10px 0  0 10px;
                    border: 1px solid #C4C4C4;
                    border-radius: 8px;
                }
                &__check {
                    margin-top: 20px;
                    text-align: left;
                    padding: 0 10px;
                    & input {
                        display: none;
                    }
                    & label {
                        margin-top: 10px;
                        position: relative;
                        &::before {
                            content: "";
                            display: inline-block;
                            position: absolute;
                            width: 28px;
                            height: 28px;
                            border: 1px solid #C4C4C4;
                            box-sizing: border-box;
                        }
                        &::after {
                            content: "";
                            display: inline-block;
                            position: absolute;
                            height: 7px;
                            width: 12px;
                            left: 9px;
                            top: 4px;
                            border-left: 2px solid;
                            border-bottom: 2px solid;
                            transform: rotate(-45deg);
                        }
                    }
                    & p {
                        margin-left: 40px;
                        text-align: left;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 17px;
                    }
                }
            }
        }
        &__error-paragraph {
            color: salmon;
            font-size: 16px;
            font-weight: 600;
            text-align: center;
            margin: 0;
            margin-top: 15px;
        }
        & input[type="checkbox"] + label::after {
            content: none;
        }
        & input[type="checkbox"]:checked + label::after {
            content: "";
        }
        & button {
            margin: 30px 0 60px 0;
            border: none;
            outline: none;
            width: 90%;
            padding: 18px;
            background: #F89F46;
            border-radius: 52px;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;
            cursor: pointer;
            @media (max-width: 1400px) {
                padding: 10px;
            }
        }
    }
}

.devices_input_disabled {
    margin-top: 40px;
    padding: 10px;
    width: 100%;
    height: 40px;
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 8px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    &__tools {
        display: flex;
        align-items: center;
        margin-right: 10px;
        & input {
            margin: 0 6px;
            width: 35px;
            height: 30px;
            padding: 0;
            text-align: center;
            border: none;
            border: 1px solid #eee;
            border-radius: 5px;
        }
        button {
            margin: 0;
            padding: 0;
            width: 30px;
            height: 30px;
            border-radius: 5px;
            background-color: rgb(40, 163, 40);
            color: white;
            font-size: 20px;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

@media screen and (max-width: 1300px) {
    .devices_input_disabled {
        &__tools {
            margin-right: 0;
        }
    }
}

@media (max-width: 1060px) {
    .right-customs {
        display: none;
        width: 0;
    }
}