.main-swiper{
  box-sizing: border-box;
  width: 100%;
  padding-top: 40px;
  @media screen and (max-width: 1060px) {
    padding-top: 10px !important;
  }
  position: relative;
  text-align: center;
  &_header{
    width: 100%;
    display: flex;
    padding-left: 15px;
    padding-bottom: 20px;
    h2{
      background: #FFFFFF;
      filter: drop-shadow(0px 0px 25px rgba(0, 0, 0, 0.2));
      border-radius: 8px;
      width: 166px;
      padding: 13px;
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 22px;
      text-align: center;
      color: #000000;
    }
  }
  &_footer{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-top: 30px;
    padding-right: 50px;
    padding-left: 5px;
    button{
      background: #F89F46;
      box-shadow: 0 4px 9px rgba(248, 159, 70, 0.7);
      border-radius: 52px;
      padding: 0 13px;
      border: none;
      cursor: pointer;
      height: 44px;
      outline: none;
      a{
        text-decoration: none;
        font-weight: bold;
        font-size: 18px;
        line-height: 29px;
        color: #FFFFFF;
      }
    }
  }
  .wrapper{
    width: 100%;
    box-sizing: border-box;
    display: flex;
    overflow-x: scroll;
    padding: 20px 0;
    position: relative !important;
    box-shadow: 0.5px 0.5px 10px 1px rgb(189, 189, 189);
    border-radius: 15px;
  }
  ::-webkit-scrollbar-track {
    background: #FFFFFF !important;
    border: 3px solid #43A948 !important;
    border-radius: 15px;
  }
  ::-webkit-scrollbar {
    height: 17px;
  }
  ::-webkit-scrollbar-thumb {
    background: #43A948;
    border-radius: 15px;
    width: 100px !important;
  }

  @media screen and (max-width: 768px) {
    padding-top: 75px;
  }
  @media screen and (max-width: 700px) {
    position: relative;
    &_header{
      max-width: 147px;
      min-width: 121px;
      h2{
        padding: 8px 0;
        font-size: 15px;
        line-height: 22px;
      }
    }
    &_footer{
      button{
        a{
          font-size: 15px;
          line-height: 22px;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .main-swiper {
    &_footer {
      button {
        height: 38px;
        a {
          font-size: 16px;
        }
      }
    }
    &_header {
      h2 {
        font-size: 18px;
      }
    }
  }
}