.modal-block-backdrop {
    width: 100%;
    height: 100%;;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.75);
    z-index: 10002;
}

.modal-block-content {
    z-index: 10005;
    max-height: 75%;
    position: fixed;
    width: 70%;
    top: 20%;
    left: 15%;
    margin-right: -50%;
    border-radius: 10px;
    padding: 15px;
    transform: translate(-50%, -50%);
    background-color: white;
    transition: all 0.3s ease-out;
    box-sizing: border-box;
    border: 1px solid #ccc;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
    &::-webkit-scrollbar-track {
        border-radius: 15px;
      }
    &::-webkit-scrollbar {
        height: 30px;
        width: 6px;
      }
    &::-webkit-scrollbar-thumb {
        background: #43A948;
        border-radius: 5px;
        width: 100px !important;
      }
}

.modal-block-opened {
    display: block;
    opacity: 1;
    visibility: visible;
    transform: translateY(+5px);
}

.modal-block-closed {
    display: none;
    visibility: hidden;
    opacity: 0;
}