.main_button {
    .header {
        h3 {
            font-size: 24px;
        }
        h1 {
            font-size: 24px;
        }
    }
}

@media screen and (max-width: 1200px) {
    .main_button {
        .header {
            h3 {
                font-size: 20px;
            }
            h1 {
                font-size: 20px;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .main_button {
        .header {
            h3 {
                font-size: 18px;
            }
            h1 {
                font-size: 18px;
            }
        }
    }
}