.open-modal{
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #43A948;
  box-sizing: border-box;
  border-radius: 8px;
  background: #FFFFFF;
  height: 35px;
  @media screen and (max-width: 610px) {
    height: 30px;
    padding: 0px 3px !important;
  }
  font-size: 14px;
  line-height: 17px;
  color: #43A948;
  outline: none;
  cursor: pointer;
  & i {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.open-modalTwo, .open-modalThree{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 197px;
  height: 47px;
  background: #F89F46;
  border: none;
  border-radius: 52px;
  font-weight: bold;
  font-size: 20px;
  line-height: 29px;
  color: #FFFFFF;
  outline: none;
  cursor: pointer;
  &_price {
    background-color: brown;
    padding: 3px 7px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 12px;
    left: -10px;
    font-size: 12px;
    color: white;
    font-weight: bold;
  }
}
.ReactModal__Content{
  overflow: visible !important;
  border: none !important;
  padding-top: 42px !important;
  background: transparent !important;
  position: relative;
  ::-webkit-scrollbar {
    height: 17px;
    width: 8px;
  }
  ::-webkit-scrollbar-thumb {
    background: #43A948;
    border-radius: 15px;
    width: 8px;
  }
  .modal-block{
    display: flex;
    flex-direction: column;
    min-width: 300px;
    height: 300px;
    overflow-y: scroll;
    border: 1px solid rgb(204, 204, 204);
    background: rgb(255, 255, 255);
    border-radius: 6px;
    padding: 10px 10px 15px 10px;
    h2{
      font-weight: bold;
      font-size: 18px;
      line-height: 22px;
    }
    .button-close{
      position: absolute;
      top: 0;
      right: 25px;
      background: #b0b834;
      color: #e3e6b6;
      border-radius: 100%;
      border: none;
      width: 30px;
      height: 30px;
      outline: none;
      cursor: pointer;
    }
    &_content{
      display: flex;
      flex-direction: column;
      li{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 5px 0 10px 0;
        margin-bottom: 10px;
        border-bottom: 1px solid rgb(204, 204, 204);
        .left{
          display: flex;
          align-items: center;
          width: 265px;
          .title-image{
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            border: none;
            width: 90px;
            height: 70px;
            border-radius: 100%;
            box-shadow: 0 0 5px rgb(204, 204, 204);
            img{
              width: 90%;
              max-height: 90%;
            }
          }
          &_text{
            display: flex;
            flex-direction: column;
            padding-left: 15px;
            width: 100%;
            h3{
              font-size: 12px;
              font-weight: bold;
              margin: 0;
              word-break: break-all;
            }
            p{
              font-weight: normal;
              font-size: 12px;
              color: gray;
              margin: 0;
            }
          }
        }
        .right{
          display: flex;
          align-items: center;
          width: 170px;
          &_text{
            display: flex;
            align-items: center;
            button {
              background-color: rgb(40, 163, 40);
              color: white;
              border-radius: 5px;
              margin: 0;
              padding: 0;
              height: 22px;
              width: 22px;
              outline: none;
              border: none;
              cursor: pointer;
            }
            span{
              padding: 0 3px !important;
              display: flex;
              align-items: center;
              p{
                border: 2px solid #43A948;
                box-sizing: border-box;
                border-radius: 8px;
                font-weight: bold;
                font-size: 18px;
                line-height: 22px;
                padding: 0 5px;
                margin: 0 !important;
              }
            }
          }
          .price{
            margin-left: auto;
            text-align: right;
            p{
              font-weight: bold;
              font-size: 14px;
              line-height: 20px;
              margin: 0;
            }
            span {
              font-size: 11px;
              text-decoration: line-through;
              font-weight: normal;
            }
          }
        }
        .center{
          display: flex;
          align-items: center;
          padding: 0 25px 0 15px;
        }

      }
    }
    &_footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 10px;
      border-bottom: 1px solid #eee;
      margin-bottom: 5px;
      .left{
        display: flex;
        flex-direction: column;
        border: none;
        background: #f4f4f4;
        padding: 5px 13px;
        border-radius: 28px;
        width: 70px;
        p{
          font-weight: normal;
          font-size: 12px;
          color: gray;
          margin: 0;
        }
        h2{
          margin: 0;
          font-weight: bold;
          font-size: 16px;
          line-height: 20px;
        }
      }
      .right{
        cursor: pointer;
        width: 180px;
        border: 1px solid rgb(212, 0, 42);
        background: #FFFFFF;
        border-radius: 15px;
        height: 30px;
        outline: none;
        transition: .3s all;
        a{
          text-decoration: none;
          font-style: normal;
          font-weight: bold;
          font-size: 12px;
          line-height: 15px;
          color: rgb(212, 0, 42);
        }
      }
      .right:hover{
        border: 1px solid #43A948;
        background: #43A948;
        a{
          color: #FFFFFF;
        }
      }
    }
  }
  .modal-block::before {
    content: '';
    position: absolute;
    right: 30px;
    top: 22.9px;
    border: 10px solid transparent;
    border-top: 10px solid rgb(204, 204, 204);

    transform: rotate(180deg);
  }
  .modal-block::after {
    content: '';
    position: absolute;
    right: 30px;
    top: 23.6px;
    border: 10px solid transparent;
    border-top: 10px solid rgb(255, 255, 255);
    transform: rotate(180deg);
  }
  @media screen and (max-width: 500px) {
    padding: 42px 0 10px 0 !important;
    .modal-block{
      padding: 10px;
      &_content{
        li{
          .center{
            padding: 0 15px;
            &_right{
              padding: 0 3px;
              img{
                max-height: 50px;
              }
              span{
                p{
                  font-size: 14px;
                  line-height: 18px;
                }
              }
            }
          }
          .price{
            p{
              font-size: 14px;
              line-height: 18px;
            }
          }
          .left{
            width: 65%;
            .title-image {
              width: 45px;
              height: 45px;
            }
            .left_text {
              width: calc(100% - 75px);
            }
          }
          .right{
            justify-content: flex-end;
            &_text{
              span{
                padding: 0 5px;
                p{
                  margin-top: 20px;
                  font-size: 14px;
                  line-height: 18px;
                }
              }
            }
            .price{
              p{
                font-weight: bold;
                font-size: 14px;
                line-height: 20px;
              }
            }
          }
        }
      }
    }
  }
}
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
  z-index: 1000000;
}
.ReactModal__Overlay--after-open{
  opacity: 1;
}
.ReactModal__Overlay--before-close{
  opacity: 0;
}

@media screen and (max-width: 450px) {
  .open-modal{
    font-size: 12px;
  }
  .open-modalTwo{
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
  }
  .open-modalThree{
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
  }
}

@media screen and (max-width: 800px) {
  .open-modalTwo {
    font-size: 16px;
  }
}