$black_color: #3F3737;
$green_color: #43A948;
.container{
  margin-bottom: 18px;
  &_list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    width: 272px;
    @media screen and (min-width: 1440px) {
      width: 300px;
    }
    @media screen and (min-width: 1350px) {
      width: 290px;
    }
    height: 426px;
    box-sizing: border-box;
    border-radius: 8px;
    margin-right: 30px;
      .list_header{
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        padding-top: 15px;
        width: 100%;
        height: 188px;
        overflow: hidden;
        cursor: pointer;
        & div {
          cursor: pointer;
          width: 90%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
            & img{
              cursor: pointer;
              width: auto;
              max-width: 100%;
              max-height: 100%;
            }
        }
      }
    .list_footer{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 238px;
      box-sizing: border-box;
      padding: 0 15px 5px 15px;
      width: 100%;
      amp-img{
        cursor: pointer;
      }
      .header{
        height: 60%;
        a{
          text-decoration: none;
          color: $black_color;
          h2{
            text-align: left;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
          }
        }

        span{
          font-weight: normal;
          font-size: 12px;
          line-height: 159.11%;
        }
      }
      .center{
        height: 15%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 5px;
        div {
          text-align: center;
          h2 {
          font-weight: bold;
          font-size: 16.5px;
          line-height: 20px;
          margin: 0;
          margin-top: -3px;
          }
          p{
            font-weight: normal;
            font-size: 13px;
            line-height: 20px;
            padding-right: 5px;
            margin: 0;
            text-decoration: line-through;
          }
        }
        span{
          display: flex;
          text-align: right;
          align-items: center;
          p{
            font-weight: normal;
            font-size: 16.5px;
            line-height: 20px;
            padding-right: 5px;
          }
        }

      }
      .footer{
        height: 25%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &_left{
          display: flex;
          align-items: center;
          & button {
            width: 35px;
            height: 35px;
            @media (max-width: 610px) {
              width: 25px;
              height: 25px;
            }
            border-radius: 8px;
            outline: none;
            border: none;
            cursor: pointer;
            color: white;
            font-size: 20px;
            background-color: #43A948;
          }
          span{
            padding: 0 5px;
            p{
              border: 2px solid #43A948;
              box-sizing: border-box;
              border-radius: 8px;
              font-weight: bold;
              font-size: 20px;
              line-height: 27px;
              padding: 0 5px;
            }
          }
        }
        i{
          cursor: pointer;
        }
      }
    }
  }
  @media screen and (min-width: 700px) and (max-width: 1750px){
    &_list{
      margin: 0 20px;
    }
  }
  @media screen and (max-width: 700px) {
    &_list{
      margin: 0;
    }
  }
  @media screen and (max-width: 610px) {
    width: 48%;
    padding: 10px 0 0 0;
    &_list {
      height: 338px;
      width: 100%;
      padding: 10px;
      box-shadow: 0 0 13.7522px rgba(0, 0, 0, 0.2);
      border-radius: 4.4007px;
      .list_header{
        height: 120px;
        padding-top: 0;
        & div {
          & img{
            max-width: 135px;
            max-height: 109px;
          }
        }
      }
      .list_footer{
        padding: 0 0 5px 0;
        height: 200px;
        amp-img{
          width: 8.8px !important;
          height: 50px !important;
        }
        .header{
          a{
            h2{
              font-size: 16px;
              line-height: 20px;
              margin: 5px 0 0 0;
            }
          }

        }
        .center {
          height: 30%;
          h2{
            font-size: 16.5px;
          }
          span{
            flex-direction: column;
            p {
              margin: 0;
              width: 52px;
              font-size: 12px;
              line-height: 14px;
              padding-right: 0;
            }
          }

        }
        .footer {
          height: 10%;
          &_left{
            img{
              width: 8.8px;
            }
            span{
              padding: 0 7px;
              p{
                font-size: 14.2119px;
                line-height: 17px;
              }
            }
          }
          &_right{
            width: 70px;
            height: 24px;
            font-size: 12px;
            line-height: 15px;
            padding: 0;
            border: 1.10018px solid #43A948;
            box-sizing: border-box;
            border-radius: 4.4007px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 450px) {
    &_list {
      width: 100%;
      .list_footer{
        .footer{
          &_left{
            span{
              padding: 0 2px;
            }
          }
          i{
            font-size: 20px;
          }
        }
      }
    }
  }
  &_block{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    width: 277.5px !important;
    height: 436px !important;
    border-radius: 8px;
    padding: 18px 28px 5px 28px;
    box-sizing: border-box;
    text-align: left;
    .list_header{
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      width: 100% !important;
      height: 45%;
      overflow: hidden;
      & div {
        & img{
          max-width: 100%;
          max-height: 190px;
        }
      }
    }
    .list_footer{
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 55%;
      width: 100% !important;
      amp-img{
        cursor: pointer;
      }
      .header{
        height: 60%;
        a{
          text-decoration: none;
          color: $black_color;
          h2{
            text-align: left;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
          }
        }
        span{
          font-weight: normal;
          font-size: 12px;
          line-height: 159.11%;
        }
      }
      .center{
        height: 15%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        div {
          text-align: center;
          h2 {
          font-weight: bold;
          font-size: 16.5px;
          line-height: 20px;
          margin: 0;
          margin-top: -3px;
          }
          p{
            font-weight: normal;
            font-size: 13px;
            line-height: 20px;
            padding-right: 5px;
            margin: 0;
            text-decoration: line-through;
          }
        }
        span{
          width: 60%;
          display: flex;
          align-items: flex-start;
          justify-content: flex-end;
          text-align: right;
          p{
            font-weight: normal;
            font-size: 16.5px;
            line-height: 20px;
            padding-right: 5px;
          }
        }

      }
      .footer{
        height: 25%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &_left{
          display: flex;
          align-items: center;
          & button {
            width: 35px;
            height: 35px;
            border-radius: 8px;
            outline: none;
            border: none;
            cursor: pointer;
            color: white;
            font-size: 20px;
            background-color: #43A948;
          }
          span{
            padding: 0 5px;
            p{
              border: 2px solid #43A948;
              box-sizing: border-box;
              border-radius: 8px;
              font-weight: bold;
              font-size: 20px;
              line-height: 27px;
              padding: 0 7px;
            }
          }
        }
        i{
          cursor: pointer;
        }
      }
    }
  }

  .triangle-for-filter-label {
    background-color: #fff; 
    border-radius: 5px;
    position: absolute;
    top: 20px;
    left: 75%;
    width: 40px;
    height: 40px;
    box-shadow: 1px 1px 8px 2px rgb(252, 200, 89);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 18px;
    z-index: 10;
    & img {
      max-width: 100%;
      max-height: 100%;
    }
    @media screen and (max-width: 390px) {
        width: 30px;
        height: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .container{
    padding: 0 1.5%;
    margin-bottom: 25px!important;
  }
}